.App {
  min-width:100%;
  min-height:600px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.contentView{
  border-width: 2px;
  border-radius: 8px;
  text-align: center;
  width: 80%;
  min-height: 500px;
  padding: 10px;
  margin: 10px;
  background-color: white;
}

.rowView{
  min-width: 100%;
  min-height: 600px;
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: whitesmoke;
}